.service {
  background-color: grey;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  margin: 1rem;
  text-align: center;
  transition: background-color 0.3s;
  color: white;
}

.dark-mode .service {
  background-color: #2c2c2c;
  border-color: #444;
  color: #ffffff;
}
